  color: #333;
}

.burrito-wrap {
  position: absolute;
  overflow-y: scroll;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  -webkit-overflow-scrolling: touch;
  -webkit-transform: translateZ(0);
  -webkit-animation: fadeIn 2s;
  animation: fadeIn 2s;
}

.go-back {
  color: #333;
  font-family: 'Luckiest Guy', 'Helvetica Neue', Arial, sans-serif, cursive;
  background-color: #ed3264;
  padding: 7px 5px 0 5px;
}

small {
  font-size: 0.666em;
  vertical-align: middle;
}

section {
  padding: 3em;
  display: flex;
  flex-direction: column;
}

h1 {
  font-family: 'Luckiest Guy', 'Helvetica Neue', Arial, sans-serif, cursive;
  font-weight: 700;
  font-size: 2.5em;
  line-height: 1.2em;
  text-transform: uppercase;
}

h2 {
  color: #999;
  font-weight: normal;
  font-size: 1em;
  letter-spacing: 0.1em;
  margin-top: -1em;
  margin-bottom: 2em;
  transform: rotate(-3deg);
}

h1,
h2 {
  position: relative;
}

h2 > span {
  background-color: #ed3264;
  color: white;
  padding: 2px;
  font-size: 1.4em;
  line-height: 1.4em;
}

h2 > span > small {
  padding-left: 2px;
}

ul {
  margin-bottom: 1.1em;
}

li {
  list-style: none;
  margin-top: 0.4em;
}

p {
  font-size: 1em;
  line-height: 2em;
  margin: 0;
  letter-spacing: 1px;
}

canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

a {
  text-transform: uppercase;
  color: #666666;
  text-decoration: none;
  transition: all 0.4s ease;
  padding: 1px;
  font-weight: 900;
}

a.disabled:hover {
  color: #b8b7b7;
  background-color: transparent;
}

a:hover {
  background-color: #ed3264;
  color: white;
}

.disabled {
  color: #999;
  font-style: italic;
  cursor: not-allowed;
}

footer {
  display: flex;
}

p.copy {
  padding-left: 3em;
}

.rotate {
  display: inline-block;
  transform: rotate(66deg);
  -webkit-transform: rotate(66deg);
  -moz-transform: rotate(66deg);
  animation: roll 2s 1;
  -webkit-animation: roll 2s 1;
  -moz-animation: roll 2s 1;
}

@keyframes roll {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(412deg);
  }
}
@-webkit-keyframes roll {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(422deg);
  }
}

.blink {
  animation: blink 0.2s infinite;
  -webkit-animation: blink 0.2s infinite;
  -moz-animation: blink 0.2s infinite;
}

.blink-slow {
  animation: blink 1s infinite;
  -webkit-animation: blink 1s infinite;
  -moz-animation: blink 1s infinite;
}

@keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes blink {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/*BROWSER CONFIG*/
@-ms-viewport {
  width: device-width;
}

::selection {
  background: #edda32;
}

span.status {
  font-size: 1em;
  font-weight: 400;
  background-color: #74f2ba;
  margin-bottom: 1.5em;
}

/*PACE LOADING PROGRESS BAR*/

.pace {
  -webkit-pointer-events: none;
  pointer-events: none;

  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
      user-select: none;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transform: translate3d(0, -50px, 0);
  transition: transform 0.5s ease-out;
}

.pace.pace-active {
  transform: translate3d(0, 0, 0);
}

.pace .pace-progress {
  display: block;
  position: fixed;
  z-index: 2000;
  top: 0;
  right: 100%;
  width: 100%;
  height: 10px;
  background: #ed3264;

  pointer-events: none;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  40%,
  100% {
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  40%,
  100% {
    opacity: 1;
  }
}

/* glitch */
.glitch-txt {
  position: relative;
}

.glitch:before,
.glitch-txt:before {
  position: absolute;
  left: -1px;
  overflow: hidden;
  clip: rect(0, 900px, 0, 0);
  text-shadow: 1px 0 #f1f1f1;
  content: attr(data-text);
